import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  QueryList,
  SimpleChanges,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { Tab, TabComponent } from '@sofico-framework/ui-kit/components/tab';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { take } from 'rxjs/operators';

@UntilDestroy()
@Component({
  selector: 'sof-tabs',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div class="snap-left">
      <button
        sofButton
        class="btn btn-plain"
        *ngIf="!inViewLeft"
        (click)="scroll(true)"
      >
        <sof-svg-icon icon="icon-chevron-left"></sof-svg-icon>
      </button>
    </div>
    <div class="tabs" #tabsView>
      <sof-in-view
        [scrollableRef]="tabsViewRef?.nativeElement"
        (inView)="inViewLeft = $event"
      ></sof-in-view>

      <sof-tab
        *ngFor="let tab of tabs; trackBy: trackByFn; let index = index"
        [tc]="tc"
        #tabCmps
        class="mr-1"
        [tab]="tab"
        [isSelected]="tab === active"
        (clickTab)="clickedTab.emit(tab)"
      ></sof-tab>
      <sof-in-view
        [scrollableRef]="tabsViewRef?.nativeElement"
        (inView)="inViewRight = $event"
      >
      </sof-in-view>
      <div class="right-spacer"></div>
    </div>
    <div class="snap-right">
      <button
        sofButton
        class="btn btn-plain"
        *ngIf="!inViewRight"
        (click)="scroll(false)"
      >
        <sof-svg-icon icon="icon-chevron-right"></sof-svg-icon>
      </button>
    </div>
  `,
  styleUrls: ['./tabs.component.scss']
})
export class TabsComponent implements OnChanges, AfterViewInit, OnDestroy {
  inViewLeft = true;
  inViewRight = true;
  @Input() tc: string;
  @Input() tabs: Tab[];
  @Input() active: Tab;
  @Output() clickedTab = new EventEmitter<Tab>();
  @ViewChild('tabsView', { read: ElementRef, static: true }) tabsViewRef;
  @ViewChildren('tabCmps') tabComponents: QueryList<TabComponent>;
  trackByFn = (i, r) => r;

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.active) {
      this.scrollToCenter();
    }
  }

  ngAfterViewInit(): void {
    // Once all the tabs are initialized, scroll to the active one
    this.tabComponents.changes
      .pipe(takeUntilDestroy(this), take(1))
      .subscribe((comps: QueryList<TabComponent>) => {
        this.scrollToCenter();
      });
  }

  ngOnDestroy(): void {}

  /**
   * Scroll a bit to the right, make factor negative for left scroll
   * @param factor scrolling factor, 1 for normal right, -1 for normal left
   */
  scroll(back: boolean): void {
    const element = this.tabsViewRef.nativeElement;
    element.scroll(
      Math.max(
        0,
        element.scrollLeft + (back ? -1 : 1) * (element.clientWidth * 0.35)
      ),
      0
    );
  }

  private scrollToCenter(): void {
    if (this.tabComponents && this.active) {
      const elem = this.tabComponents.find(x => x.tab === this.active)
        ?.elementRef?.nativeElement;
      elem?.scrollIntoView({
        block: 'nearest',
        inline: 'center',
        behavior: 'smooth'
      });
    }
  }
}
